@use "../breakpoints" as *;

.additional-nav {
  display: none;
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;

  @media (min-width: $desktop-width) {
    display: flex;
    gap: 60px;
    grid-column: 2 / 4;
  }
}

.additional-nav__item {
  display: flex;
  align-items: center;
}

.additional-nav__link {
  font-size: 15px;
  font-weight: 700;
  line-height: 24px;
  color: var(--text-color);
  text-decoration: none;

  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  width: 100%;

  &:hover {
    opacity: 0.6;
  }

  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}
